  font-size: var(--medium-font-size);
  padding: 1rem; }

/* -------------------------

Title: image
Descr: 
    Most basic universal image styles

--------------------------- */
img,
picture {
  display: block;
  height: auto;
  max-width: 100%; }

/* -------------------------

Title: campfire
Descr: 
    sets up main campfire, three columns, and responsive breakdown

--------------------------- */
.campfire {
  display: grid;
  grid-gap: var(--space);
  grid-row-gap: 0;
  grid-template-columns: 1fr;
  margin-bottom: var(--component-bottom-margin); }
  .campfire > div {
    display: flex;
    flex-direction: column; }
  @media (min-width: 782px) {
    .campfire {
      grid-template-columns: repeat(3, 1fr);
      grid-template-areas: "one one two" "three three three"; }
      .campfire__articles:nth-child(2n+2) {
        grid-area: two; }
      .campfire__articles:nth-child(3n+3) {
        grid-area: three;
        display: grid;
        grid-gap: var(--space);
        grid-row-gap: 0;
        grid-template-columns: repeat(auto-fill, minmax(var(--auto-grid-min-size), 1fr));
        grid-auto-flow: dense; }
      .campfire__feature {
        grid-area: one; } }
  @media (min-width: 1224px) {
    .campfire {
      grid-template-columns: repeat(3, 1fr) repeat(6, 0.75fr) repeat(3, 1fr);
      grid-template-areas: "one one one two two two two two two three three three"; }
      .campfire__articles:nth-child(2n+2) {
        grid-area: one; }
      .campfire__articles:nth-child(3n+3) {
        grid-area: three; }
      .campfire__feature {
        grid-area: two; } }
  @media (min-width: 1432px) {
    .campfire {
      gird-template-columns: repeat(12, 1fr); } }

.campfire__support {
  box-shadow: 1px 1px 4px 0px rgba(0, 0, 0, 0.08);
  border-radius: var(--border-radius);
  background: var(--gray-lightest);
  padding: var(--space);
  margin-bottom: 2rem; }
  @media (min-width: 1432px) {
    .campfire__support p {
      font-size: 1.25rem; } }
  .campfire__support h2 {
    font-weight: var(--heavy);
    margin-top: .5rem; }

